.btn {
	&:not(.btn-sm):not(.btn-lg) {
		@include max-w(md) {
			font-size: 14px;
			padding: 10px 15px;
		}
	}

	&-light {
		@include button-variant($white, $white, $gray-200, $gray-200, $gray-200, $gray-200);

		color: $primary;
	}

	&-sm {
		font-weight: 400;
	}

	&-collapse {
		box-shadow: 0px 2px 12px rgba(82, 63, 46, 0.1) !important;

		i {
			display: inline-block;
			font-size: 0.4em;
			vertical-align: middle;
			margin-left: 5px;
			transition: transform 0.3s;
		}

		&[aria-expanded="true"] {
			i {
				transform: rotate(180deg);
			}
		}
	}
}

.link-with-icon {
	position: relative;
	display: inline-block;
	padding-left: 35px;
	color: $gray-800;
	font-size: 16px;
	line-height: 1.25;

	&__icon {
		color: $primary;
		font-size: 20px;
		position: absolute;
		top: 0;
		left: 0;
		transition: color 0.3s;
	}

	&__text {
		display: inline-block;
		line-height: inherit;
	}

	&:hover {
		text-decoration: none;
		color: $gray-800;

		.link-with-icon__icon {
			color: $gray-800;
		}
	}
}

.icon-link {
	display: inline-flex;
	line-height: 1;
	transition: color 0.3s;
	color: $primary;
	font-size: 20px;

	&:hover {
		text-decoration: none;
		color: $gray-800;
	}
}

.link-with-duble-arrow {
	position: relative;
	padding-right: 28px;
	font-size: 16px;

	&:after {
		content: '\e905';
		position: absolute;
		top: 0;
		right: 0;
		font-family: icomoon!important;
		display: inline-block;
	}
}