.form-control {
	text-overflow: ellipsis;
}

.form-group {
	label {
		font-weight: 500;
		font-size: 12px;
		line-height: 1.1;
	}
}

.form-list {
	margin-bottom: 44px;

	.form-group {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.subscribe-form {
	position: relative;
	max-width: 400px;

	&__input {
		padding-right: 65px;
	}

	&__submit-btn {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 50px;
		border: none;
		background-color: transparent;
		color: $primary;
		font-size: 18px;
		transition: color 0.3s;

		&:hover {
			color: lighten($primary, 10%);
		}
	}
}

.form-block {
	border-radius: $border-radius;
	background-color: $white;
	padding: 30px 15px;
	box-shadow: 0px 4px 22px rgba(82, 63, 46, 0.16);

	@include min-w(md) {
		max-width: 470px;
		margin: 0 auto;
		padding: 48px 36px;
	}

	&__title-holder {
		margin-bottom: 24px;
	}
}

input {
	&[type="number"] {
		-moz-appearance: textfield;

		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}
}