.table-pricing {
	margin-bottom: 0;

	@include max-w(lg) {
		font-size: 12px;
	}

	th {
		font-weight: 400;
	}

	thead {
		th {
			vertical-align: top;
			border-bottom: none;
			border-top: none;
		}

		.table-pricing__header-main-colls {
			th {
				&:nth-child(2n + 1) {
					background-color: $gray-200;
				}
			}
		}
	}

	tbody {
		tr {
			th, td {
				border-color: $gray-100;
				padding: 8px 12px;
				vertical-align: middle;
				height: 61px;

				[class^="icon"] {
					font-size: 1.428em;
				}
			}

			&:last-child {
				th, td {
					border-bottom: 1px solid $gray-100;
				}
			}

			th {
				color: $gray-950;
				width: 220px;
				min-width: 220px;

				@include min-w(lg) {
					width: 292px;
					min-width: 292px;
				}
			}

			td {
				width: 20%;
				min-width: 175px;
				text-align: center;

				&:nth-of-type(2n + 1) {
					background-color: $gray-200;
				}
			}
		}
	}

	@for $i from 1 through 5 {
		&[data-selected-tarif="#{$i}"] {
			thead {
				.table-pricing__header-main-colls {
					th {
						&:nth-child(#{$i}) {
							background-color: $yellow;
						}
					}
				}
			}

			tbody {
				tr {
					td {
						&:nth-of-type(#{$i}) {
							background-color: $yellow;

							.icon-check-square {
								color: $primary !important;
							}

							.icon-close-square {
								color: $gray-1 !important;
							}
						}
					}
				}
			}
		}
	}

	&__main-title {
		font-family: $font-family-base;
		font-weight: 600;
		font-size: 14px;
		line-height: 1.6;
		text-transform: uppercase;

		@include min-w(md) {
			font-size: 16px;
		}

		@include min-w(lg) {
			font-size: 18px;
		}
	}

	&__first-col {
		padding: 0 !important;
		width: 220px;
		min-width: 220px;

		@include min-w(lg) {
			width: 292px;
			min-width: 292px;
		}
	}

	&__first-col-content {
		background-color: $gray-200;
		margin: 0 12px 12px 0;
		padding: 40px 24px 32px;

		@include min-w(lg) {
			margin: 0 24px 24px 0;
		}

		.table-pricing__main-title {
			margin-bottom: 83px;
		}
	}

	&__first-col-title {
		margin-bottom: 85px;
	}

	&__first-col-text {
		margin-bottom: 16px;
		padding-right: 35px;
	}

	&__top-line-block {
		position: relative;
		text-align: center;
		background-color: $gray-200;
		height: 50px;
		padding: 10px 15px !important;
		border-bottom: 12px solid $white !important;

		@include min-w(lg) {
			border-bottom-width: 16px !important;
		}
	}

	&__attention-circle {
		@include sizeElement(114px);
		@include flexCenter;

		position: absolute;
		bottom: 10px;
		right: 0;
		border-radius: 50%;
		background-color: $orange-faded;
		transform: rotate(15deg);
		font-weight: 600;
		font-size: 14px;
		line-height: 1.4;
		text-align: center;
		color: $gray-950;
	}

	&__header-main-colls {
		text-align: center;

		th {
			position: relative;
			width: 20%;
			min-width: 175px;
			padding: 48px 12px 20px;
		}
	}

	&__tarif-title {
		margin-bottom: 20px;
	}

	&__pricing-title {
		margin-bottom: 20px;
	}

	&__tarif-desc {
		padding-top: 16px;
		text-align: left;
		padding-right: 10px;
	}

	&__tarif-badge {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 5;
		width: 100%;
		text-align: center;
		color: $white;
		background-color: $cyan-dark;
		font-weight: 500;
		font-size: 12px;
		padding: 5px 10px;
		text-transform: uppercase;
	}
}