.people-info-block {
	position: relative;
	display: flex;

	&.-with-rating {
		padding-top: 30px;

		@include min-w(sm) {
			padding-right: 130px;
			padding-top: 0;
		}
	}

	&__photo {
		@include sizeElement(60px);

		position: relative;
		flex-shrink: 0;
		margin-right: 16px;

		&:after {
			content: '';
			display: block;
			padding-bottom: 100%;
		}
	}

	&__info {
		padding-top: 5px;
	}

	&__name-and-value {
		font-weight: 600;
		font-family: $font-family-base;
		color: $gray-950;
		margin-bottom: 5px;

		span {
			font-weight: 300;
			font-style: italic;
		}
	}

	&__bottom-text {
		color: $gray-950;
		margin-bottom: 0;
	}

	&__rating {
		position: absolute;
		top: 0;
		z-index: 5;

		@include min-w(sm) {
			top: 20px;
			right: -8px;
		}
	}
}