.slick-vertical {
	.slick-slide {
		border: none;
	}
}

.reviews-slider-block {
	background-color: $white;
	box-shadow: 0px 4px 22px rgba(82, 63, 46, 0.16);
	border-radius: $border-radius;
	padding: 24px 15px;

	@include min-w(sm) {
		padding: 24px;
	}

	@include min-w(md) {
		padding: 165px 24px 40px;
	}
}

.reviews-slider {
	padding: 0 0 70px 0;

	@include min-w(sm) {
		padding-left: 132px;
	}

	.slick-slide {
    margin: 0 12px;
  }

  .slick-list {
    margin: 0 -12px;
  }

	.slick-arrow {
		transform: none;
		top: auto;
		bottom: 0;

		&.slick-prev {
			left: 0;
		}

		&.slick-next {
			left: 52px;
		}
	}
}

.slider-team {
	padding-bottom: 50px;

	@include min-w(sm) {
		padding-bottom: 0;
	}

	.slick-slide {
    margin: 0 12px;
  }

  .slick-list {
    margin: 0 -12px;
  }

	.slick-arrow {
		transform: none;
		top: auto;
		left: auto;
		bottom: 0;

		&.slick-prev {
			right: 52px;
		}

		&.slick-next {
			right: 0;
		}
	}

	&__text {
		margin-bottom: 50px;
	}

	&__team-member-info {
		@include min-w(sm) {
			padding-right: 100px;
		}
	}
}

.customer-reviews-slider {
	padding-top: 60px;

	@include min-w(sm) {
		padding-left: 76px;
		padding-top: 0;
	}

	.slick-arrow {
		transform: none;

		&.slick-prev {
			top: 0;
			left: 0;

			@include min-w(sm) {
				bottom: 92px;
				top: auto;
			}
		}

		&.slick-next {
			top: 0;
			left: 52px;

			@include min-w(sm) {
				bottom: 40px;
				left: 0;
				top: auto;
			}
		}
	}

	.slick-list {
		height: 350px !important;

		@include min-w(sm) {
			height: 470px !important;
		}
	}
}