.slick-prev, .slick-next {
	width: $slick-arrow-width;
	height: $slick-arrow-height;
	background-color: $slick-arrow-bg;
	border-radius: 50%;
	z-index: 1;
	transition: border-color 0.3s;
	border: 2px solid transparent;

	&:before {
		transition: 0.5s;
		font-size: $slick-arrow-size;
		font-weight: $slick-arrow-weight;
	}

	&:hover, &:focus {
		background-color: $slick-arrow-bg;
	}

	&.slick-disabled {
		cursor: default;
	}

	&:not(.slick-disabled) {
		&:hover {
			border-color: $primary;
		}
	}
}

.slick-prev {
	left: -25px;
}

.slick-next {
	right: -25px;
}

.slick-slider {
	&.slick-vertical {
		.slick-arrow {
			&.slick-prev {
				&:before {
					content: '\e90c';
				}
			}

			&.slick-next {
				&:before {
					content: '\e90d';
				}
			}
		}
	}
}