.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	z-index: $zindex-fixed;
	background-color: $white;
	padding: 15px 0;
	border-bottom: 1px solid $gray-100;

	@include min-w(md) {
		padding: 20px 0;
	}

	&__main-content {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__right-content {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		flex-grow: 1;
	}

	&__lang-switch {
		margin-right: 50px;
	}

	&__actions-block {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
		flex-grow: 1;
	}

	&__logo {
		display: inline-block;
		line-height: 1;
		margin-right: 15px;
		max-width: 32px;

		@include min-w(sm) {
			max-width: 180px;
		}

		@include min-w(md) {
			max-width: 220px;
			margin-right: 30px;
		}

		@include min-w(lg) {
			max-width: 290px;
		}
	}

	&__contacts-list {
		margin-right: 38px;
		display: none;

		@include min-w(xl) {
			display: block;
		}
	}

	&__sign-in-link-block {
		margin-right: 20px;

		@include min-w(md) {
			margin-right: 30px;
		}

		@include min-w(lg) {
			margin-right: 36px;
		}
	}

	&__btn-block {
		margin-right: 30px;
		display: none;

		@include min-w(md) {
			display: block;
		}

		@include min-w(lg) {
			margin-right: 52px;
		}
	}

	&__sign-in-link {
		font-size: 14px;
		line-height: 1.25;
		color: $gray-800;
		margin: 5px 0;

		@include min-w(md) {
			font-size: 16px;
		}

		&:hover {
			color: $gray-800;
		}
	}
}