.accordion-with-icons {
	box-shadow: 0px 4px 22px rgba(82, 63, 46, 0.16);
	border-top-left-radius: $border-radius;
	border-top-right-radius: $border-radius;
	overflow: hidden;

	&__item {
		position: relative;
		background-color: $white;
		transition: background-color 0.3s, padding 0.3s;
		padding: 40px 15px 40px 80px;

		@include min-w(sm) {
			padding: 40px 30px 40px 120px;
		}

		@include min-w(lg) {
			padding: 80px 70px 80px 120px;
		}

		@include min-w(xl) {
			padding-left: 200px;
		}

		&.-active {
			background-color: $gray-200;

			@include min-w(lg) {
				padding-top: 48px;
				padding-bottom: 48px;
			}
		}
	}

	&__icon {
		position: absolute;
		top: 50%;
		left: 20px;
		width: 35px;
		text-align: center;
		transform: translate(0, -50%);

		@include min-w(sm) {
			left: 35px;
			width: 50px;
		}

		@include min-w(xl) {
			left: 75px;
			width: 80px;
		}
	}

	&__title {
		margin-bottom: 0;
		font-family: $font-family-base;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1px;
	}

	&__btn {
		text-transform: inherit;
		padding: 0;
		border: none;
		margin: 0;
		background-color: transparent;
		font-weight: inherit;
		font-family: inherit;
		color: $gray-800;
		letter-spacing: inherit;
		text-align: left;
		transition: color 0.3s;

		&:hover {
			color: $primary;
		}

		&:after {
			@include absoluteCover;

			content: '';
			display: block;
		}
	}

	&__body {
		line-height: 1.7;
		padding-top: 16px;
	}
}