.section {
	padding: 40px 0;

	@include min-w(md) {
		padding: 60px 0;
	}

	@include min-w(xl) {
		padding: 80px 0;
	}

	&-welcome {
		background-color: $blue;
		padding: 60px 0;

		@include min-w(md) {
			padding-bottom: 80px;
		}

		@include min-w(lg) {
			padding-bottom: 115px;
		}

		+ section {
			@include min-w(lg) {
				padding-top: 150px;
			}

			@include min-w(xl) {
				padding-top: 275px;
			}
		}

		h1 {
			text-shadow: 2px 0 0 currentColor;
		}

		&__main-content {
			margin-bottom: 50px;

			@include min-w(md) {
				padding: 30px 40px 0 0;
				margin-bottom: 0;
			}

			@include min-w(lg) {
				padding-top: 80px;
			}
		}

		&__right-content {
			position: relative;
		}

		&__main-img-block {
			@include responsive-ratio(472, 520);

			position: relative;
			z-index: 5;
			border-radius: $border-radius;
			overflow: hidden;
			margin-bottom: 30px;

			@include min-w(md) {
				margin-bottom: 0;
			}
		}

		&__description {
			max-width: 550px;
			margin-bottom: 24px;
		}

		&__btn-block {
			padding-top: 30px;

			@include min-w(md) {
				padding-top: 60px;
			}
		}

		&__reviews-slider {
			@include min-w(md) {
				position: absolute;
				top: 100%;
				left: -24px;
				width: 780px;
				transform: translate(0, -142px);
				z-index: 2;
			}

			@include min-w(lg) {
				width: 1068px;
			}

			@include min-w(xl) {
				left: -50px;
			}
		}
	}

	&-get-consult {
		background-image:
			url('../img/backgrounds/section-get-consult-bg-1.svg'),
			url('../img/backgrounds/section-get-consult-bg-2.svg'),
			url('../img/backgrounds/section-get-consult-bg-3.svg'),
			url('../img/backgrounds/section-get-consult-bg-4.svg'),
		;
		background-repeat: no-repeat;
		background-position:
			center,
			top 0 left 160px,
			top 35% right 20%,
			left 23% bottom 5%;
		background-size: contain, 422px, 71px, 53px;
	}

	&-image-and-content {
		background-image:
			url('../img/backgrounds/section-image-and-content-bg-1.svg'),
			url('../img/backgrounds/section-image-and-content-bg-2.svg'),
			url('../img/backgrounds/section-image-and-content-bg-3.svg'),
		;
		background-repeat: no-repeat;
		background-position:
			right 18% top 10%,
			right 0 top 40%,
			top 55% left 45%;
		background-size: 50px, 200px, 65px;

		&__list-of-items {
			.image-and-content-block {
				&:first-child {
					padding-top: 0;
				}

				&:last-child {
					padding-bottom: 0;
				}

				&:nth-child(2n) {
					.row {
						@include min-w(md) {
							flex-direction: row-reverse;
						}
					}

					.image-and-content-block__img-block-wrap {
						@include min-w(lg) {
							margin-right: 0;
							margin-left: 42px;
						}
					}
				}
			}
		}
	}

	&-banner {
		background-repeat: no-repeat;
		background-position: left center;
		background-size: contain;

		&.bg-1 {
			background-image: url("../img/backgrounds/section-banner-bg-version-1.svg");
			background-position: left center;
		}

		&.bg-2 {
			background-image: url("../img/backgrounds/section-banner-bg-version-2.svg");
			background-position: center;
		}

		&__item {
			background-color: $orange-faded;
			padding: 30px 15px;
			text-align: center;

			@include min-w(sm) {
				padding: 30px;
			}

			@include min-w(md) {
				text-align: left;
			}

			@include min-w(md) {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}

			@include min-w(lg) {
				padding: 60px;
			}
		}

		&__title {
			font-family: $font-family-base;
			font-weight: 500;

			@include min-w(md) {
				padding-right: 20px;
				margin-bottom: 0;
			}
		}

		&__btn {
			@include min-w(md) {
				flex-shrink: 0;
			}
		}
	}

	&-premium-video {
		background-image:
			url("../img/backgrounds/section-premium-video-bg-1.svg"),
			url("../img/backgrounds/section-premium-video-bg-2.svg"),
			url("../img/backgrounds/section-premium-video-bg-3.svg"),
		;
		background-repeat: no-repeat;
		background-position:
			top 5% right 18%,
			bottom 20% left 15%,
			bottom 2% right 0
		;
		background-size: 114px, 69px, 400px;

		&__description {
			max-width: 700px;
			margin: 0 auto 30px;
		}

		&__video {
			margin-bottom: 28px;
		}
	}

	&-image-and-slider {
		background-image: url("../img/backgrounds/section-image-and-slider-bg.svg");
		background-repeat: no-repeat;
		background-position: right top;
		background-size: 300px;

		&__img-block {
			@include min-w(lg) {
				padding-right: 40px;
			}
		}

		&__img {
			border-radius: $border-radius;
		}

		&__slider {
			margin-bottom: 40px;

			@include min-w(sm) {
				margin-bottom: 80px;
			}
		}

		&__main-content {
			padding-top: 45px;
		}
	}

	&-benefits {
		background-image:
			url("../img/backgrounds/section-benefits-bg-1.svg"),
			url("../img/backgrounds/section-benefits-bg-2.svg"),
			url("../img/backgrounds/section-benefits-bg-3.svg")
		;
		background-position:
			top 20% right 10%,
			bottom 0% right 20%,
			top 3% left 20%
		;
		background-size: 57px, 418px, 189px;
		background-repeat: no-repeat;

		&__side-block {
			position: relative;
			z-index: 5;
			background-color: $gray-200;
			border-radius: $border-radius;
			padding: 50px 15px;

			@include min-w(sm) {
				padding: 80px 40px 60px;
			}

			@include min-w(lg) {
				margin-top: 115px;
			}
		}

		&__side-block-text {
			font-weight: 500;
			font-style: italic;
			margin-bottom: 50px;
		}

		&__main-content {
			position: relative;
			padding: 50px 15px;

			@include min-w(sm) {
				padding: 75px 40px;
			}

			@include min-w(lg) {
				padding: 145px 0 145px 30px;
			}

			&:before {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				width: 100%;
				display: block;
				background-color: $blue;
				background-image: url("../img/backgrounds/section-benefits-blue-box-bg.svg");
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				z-index: 1;
				pointer-events: none;

				@include min-w(lg) {
					width: 130%;
					background-size: contain;
				}
			}
		}

		&__main-content-inner {
			position: relative;
			z-index: 5;

			@include min-w(lg) {
				max-width: 540px;
				margin-left: auto;
			}
		}

		&__description {
			font-weight: 500;
			color: $gray-800;

			@include min-w(lg) {
				margin-left: 80px;
				max-width: 315px;
			}
		}
	}

	&-with-video {
		background-image:
			url("../img/backgrounds/section-with-video-bg-1.svg"),
			url("../img/backgrounds/section-with-video-bg-2.svg")
		;
		background-size:
			300px,
			contain
		;
		background-position:
			top 2% right 18%,
			bottom center
		;
		background-repeat: no-repeat;

		@include min-w(lg) {
			background-size:
				300px,
				75%
			;
		}

		&__description {
			margin-bottom: 32px;
			padding-right: 15px;
		}

		&__btn-block {
			padding-top: 50px;

			@include min-w(lg) {
				padding-top: 80px;
			}
		}

		&__main-content {
			margin-bottom: 50px;

			@include min-w(md) {
				margin-bottom: 0;
			}
		}
	}

	&-about {
		background-image: url("../img/backgrounds/section-about-bg-1.svg");
		background-repeat: no-repeat;
		background-position: bottom 2% right 38%;
		background-size: 63px;

		&__main-content {
			padding-top: 65px;
			background: linear-gradient(to bottom, $blue 450px, rgba($blue, 0) 450px) !important;
		}

		&__accordion-content {
			padding-top: 50px;

			@include min-w(md) {
				padding-top: 75px;
			}
		}

		&__right-content {
			@include min-w(md) {
				padding-left: 25px;
			}
		}
	}

	&-learn-strategies {
		background-image:
			url("../img/backgrounds/section-learn-strategies-bg-1.svg"),
			url("../img/backgrounds/section-learn-strategies-bg-2.svg"),
		;
		background-repeat: no-repeat;
		background-position:
			right 28% top 2%,
			bottom 0 right 0
		;
		background-size: 46px, 400px;


		&__img {
			max-width: 530px;
			width: 100%;
		}

		&__description {
			margin-bottom: 50px;

			strong {
				font-weight: 600;
			}
		}

		&__right-content {
			padding-top: 40px;
		}
	}

	&-logos {
		&__main-content {
			border-top: 2px solid #E4E8ED;
			border-bottom: 2px solid #E4E8ED;
			padding: 30px 0;
		}
	}

	&-customer-reviews {
		background-image: url("../img/backgrounds/section-customer-reviews-bg.svg");
		background-repeat: no-repeat;
		background-position: bottom 0 left -250px;
		background-size: 400px;
		padding-bottom: 0;

		@include min-w(lg) {
			background-position: bottom 0 left 25%;
		}

		@include min-w(xl) {
			background-size: 500px;
		}

		&__main-content {
			padding-bottom: 50px;

			@include min-w(lg) {
				padding-bottom: 100px;
			}
		}

		&__description {
			max-width: 400px;
			margin-bottom: 50px;
		}

		&__slider {
			@include min-w(xl) {
				margin-left: -52px;
			}
		}
	}

	&-items-with-icon-and-image {
		background-image:
			url("../img/backgrounds/section-items-with-icon-and-image-bg-1.svg"),
			url("../img/backgrounds/section-items-with-icon-and-image-bg-2.svg")
		;
		background-size: 46px, contain;
		background-position:
			left 45% top 2%,
			center
		;
		background-repeat: no-repeat;

		&__list-of-items {
			margin-bottom: 50px;

			@include min-w(md) {
				margin-bottom: 0;
			}

			> *:last-child {
				margin-bottom: 0;
			}
		}
	}

	&-pricing {
		padding-top: 60px;

		&__collapse-btn-block {
			margin: 20px 0;
		}
	}
}