.image-and-content-block {
	.row {
		@include max-w(md) {
			flex-direction: column-reverse;
		}
	}

	&__img-block-wrap {
		position: relative;

		@include min-w(lg) {
			margin-right: 42px;
		}
	}

	&__img-block {
		position: relative;
		padding-bottom: 100%;
		border-radius: $border-radius;
		overflow: hidden;
	}

	&__img-label {
		position: absolute;
		z-index: 5;
		display: inline-block;
		color: $cyan;
		background-color: $white;
		border-radius: $border-radius;
		padding: 15px 20px 10px;
		box-shadow: 0px 13px 26px rgba(17, 18, 22, 0.1);
		margin-bottom: 0;

		@include min-w(lg) {
			padding: 23px 36px 17px;
		}

		&.-bottom-left {
			bottom: 15px;
			left: 15px;

			@include min-w(sm) {
				left: -15px;
			}

			@include min-w(lg) {
				bottom: 50px;
				left: -30px;
			}

			@include min-w(1500) {
				left: -75px;
			}
		}

		&.-top-right {
			top: 15px;
			right: 15px;

			@include min-w(sm) {
				right: -15px;
			}

			@include min-w(lg) {
				right: -30px;
				top: 57px;
			}

			@include min-w(1500) {
				right: -98px;
			}
		}
	}

	&__content {
		padding-bottom: 30px;

		@include min-w(md) {
			padding-top: 30px;
			padding-bottom: 0;
		}

		@include min-w(lg) {
			padding-top: 76px;
		}
	}

	&__title {
		@include min-w(md) {
			max-width: 450px;
		}
	}

	&__description {
		margin-bottom: 30px;

		@include min-w(md) {
			margin-bottom: 50px;
		}
	}
}