@include title-sizes();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

.normal-text {
	font-size: $font-size-lg;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	mark {
		padding-top: 0;
		background: linear-gradient(to bottom, rgba($blue-2, 0) 40%, $blue-2 40%);
		color: inherit;

		.inverted-mark & {
			background: linear-gradient(to bottom, rgba($white, 0) 40%, rgba($white, 0.45) 40%);
		}
	}
}

h1, .h1 {
	margin-bottom: 20px;

	@include min-w(md) {
		margin-bottom: 40px;
	}

	mark {
		padding: 0 0.075em 0.075em;
	}

	.badge {
		@include min-w(lg) {
			margin-left: 20px;
		}
	}
}

h2, .h2 {
	margin-bottom: 25px;

	@include min-w(md) {
		margin-bottom: 32px;
	}

	mark {
		padding: 0 0.075em 0.075em;
	}
}

h3, .h3 {

}

h4, .h4 {
	margin-bottom: 16px;

	mark {
		padding: 0 0.075em 0;
	}
}

h5, .h5 {
	margin-bottom: 20px;
}

h6, .h6 {

}

.title-with-bg {
	font-family: $font-family-base;
	font-weight: 500;
	font-size: 12px;
	background-color: $gray-2;
	text-align: center;
	text-transform: uppercase;
	padding: 9px 15px;
	margin-bottom: 16px;
}

.numbered-list {
	@include listreset;

	margin-bottom: 50px;
	counter-reset: numbered-list;

	li {
		display: flex;

		&:not(:last-child) {
			margin-bottom: 35px;
		}

		&:before {
			@include sizeElement(40px);
			@include flexCenter;

			counter-increment: numbered-list;
			content: counter(numbered-list);
			border-radius: 50%;
			background-color: $blue-3;
			font-size: 14px;
			font-weight: 600;
			padding: 5px;
			color: $white;
			flex-shrink: 0;
			margin-right: 20px;
		}
	}
}