.reviews-card {
	padding-top: 30px;

	&__wrap {
		position: relative;
		border-radius: $border-radius;
		background-color: $gray-200;
		padding: 12px 20px 15px;
		text-align: right;
	}

	&__photo-block {
		position: absolute;
		top: 0;
		left: 20px;
		transform: translate(0, -50%);
		width: 60px;
		height: 60px;
		border: 3px solid $white;
		border-radius: 50%;
		overflow: hidden;
	}

	&__title {
		font-family: $font-family-sans-serif;
		font-weight: 500;
		font-size: 14px;
		margin-bottom: 10px;
		padding-left: 70px;
	}

	&__description {
		color: $gray-600;
	}
}

.customer-reviews-card {
	padding: 30px 15px;
	border: 1px solid $gray-100;
	background-color: $white;
	border-radius: $border-radius;
	margin-bottom: 16px;

	@include min-w(xl) {
		padding: 36px;
	}

	&__text {
		padding-top: 20px;
	}
}