.footer {
	background-color: $gray-200;
	padding: 50px 0 32px;

	@include min-w(md) {
		padding-top: 80px;
	}

	a {
		color: $gray-800;
	}

	&__logo {
		display: inline-block;
		line-height: 1;
		max-width: 230px;
		margin-bottom: 32px;

		@include min-w(sm) {
			max-width: 289px;
		}
	}

	&__description {
		font-size: 12px;
		line-height: 1.6666;
		color: $gray-600;
		margin-bottom: 40px;

		@include min-w(md) {
			max-width: 345px;
		}
	}

	&__text {
		margin-bottom: 12px;

		strong {
			font-weight: 500;
		}
	}

	&__left-col {
		margin-bottom: 50px;

		@include min-w(md) {
			margin-bottom: 0;
		}
	}

	&__right-col {
		@include min-w(md) {
			padding-left: 30px;
		}

		@include min-w(xl) {
			padding-left: 70px;
		}
	}

	&__bottom-line {
		padding-top: 40px;
	}

	&__bottom-line-nav-list {
		@include min-w(lg) {
			margin-bottom: 0;
		}
	}

	&__copyright {
		font-size: 15px;
		color: $gray-1;
		margin-bottom: 0;
	}
}

.footer-nav-list {
	@include listreset;

	margin-bottom: 15px;

	+ .footer-nav-list {
		margin-top: 45px;
	}

	&.horizontal {
		display: flex;
		flex-wrap: wrap;

		.footer-nav-list__item {
			margin: 5px 0;

			&:not(:last-child) {
				margin-right: 24px;
			}
		}
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 18px;
		}
	}
}